
import ReportLayout from "@/apps/HTS/views/reports/templates/Layout.vue"
import { defineComponent, onMounted, PropType, watch } from "vue";
import {
  IonPage,
} from "@ionic/vue"
import { HtsReportComposable } from "@/apps/HTS/composables/HtsReportComposable";
import { HtsReportColumns, ReportTemplateParams } from "@/apps/HTS/types/ReportTypes";
import { isEmpty } from "lodash";
import { toExportableFormat } from "@/components/DataViews/tables/ReportDataTable";
import { toCsv, toTablePDF } from "@/utils/Export";
import ReportTable from "@/components/DataViews/tables/ReportDataTable.vue"
import { refresh } from "ionicons/icons";
import table from "@/components/DataViews/tables/ReportDataTable"

export default defineComponent({
    components: {
        IonPage,
        ReportTable,
        ReportLayout
    },
    props: {
        params: {
            type: Object as PropType<ReportTemplateParams>,
            required: true
        }
    },
    setup(props) {
        const report = HtsReportComposable(props.params.title)
        function configureReport() {
            report.setReportPeriod({
                isPeriodSelection: props.params.hasPeriodSelection || false,
                isMonthlySelection: props.params.hasMonthlySelection || false,
                minDate: props.params.minDate,
                maxDate: props.params.maxDate,
                initialSort: props.params.initialSort, 
                generate: (data: any, formData: Record<string, any>, computedData: Record<string, any>) => {
                    const req = props.params.data(data, formData, computedData)
                    if (typeof props.params.period === 'function') {
                        report.reportData.period = props.params.period()
                    }
                    return req
                }
            })
        }
        
        onMounted(() => {
            if (isEmpty(report.reportData.data)) {
                if (typeof props.params.prePopulate === 'function') {
                    report.reportData.prePopulateData = props.params.prePopulate()
                }
                configureReport()
                report.reportData.drillDownLabelPattern = props.params.drillDownLabelPattern
                report.reportData.intermediateColumns = props.params.columns
                report.reportData.other = {
                    activeColumns: [], activeRows: []
                }
            }            
        })

        watch(() => report.reportData.intermediateColumns , (columns) => {
            report.reportData.columns = []
            columns.forEach((columnRow: HtsReportColumns[]) => {
                const columnData: any = []
                columnRow.forEach((column: HtsReportColumns) => {
                    columnData.push(table.thTxt(column.label, { 
                        value: column.exportLabel ? column.exportLabel(column.label) : undefined,
                        colspan: column.colSpan,
                        exportable: typeof column.exportable === 'boolean' 
                            ? column.exportable
                            : true
                    }))
                })
                !isEmpty(columnData) && report.reportData.columns.push(columnData)
            })
        }, { deep: true })

        watch(() => report.reportData.data, (data) => {
            report.reportData.rows = []
            data.forEach((d: any) => {
                const row: any = []
                report.reportData.intermediateColumns.forEach((columnRow: HtsReportColumns[]) => {
                    columnRow.forEach(column => {
                        let td = {}
                        const conf = column.columnTypeConfig || {}
                        if (typeof column.value !='function') {
                            return
                        }
                        switch (`${column?.columnType}`) {
                            case 'text':
                                td = table.td(column.value(d), conf)
                                break
                            case 'date':
                                td = table.tdDate(column.value(d), conf)
                                break
                            case 'drilldown':
                                td = report.drill(
                                    column.drillDownLabelPattern
                                        ? column.drillDownLabelPattern(column.label, d)
                                    : report.reportData.drillDownLabelPattern
                                        ? report.reportData.drillDownLabelPattern(column.label, d)
                                    : column.label,
                                    column.value(d) || []
                                )
                                break
                            case 'customDrillDown':
                                if (typeof column.drillParams === 'function') {
                                    td = report.drillCustom(column.value(d), {
                                        ...column.drillParams(d, report.reportData)
                                    }, column.staticValue ? column.staticValue(d) : undefined)
                                } else {
                                    td = table.td('__MISSING_CUSTOM_DRILL_DOWN__')
                                }
                                break;
                            default:
                                td = table.td(column.value(d), conf)
                                break
                        }
                        row.push(td)
                    })
                })
                if (!isEmpty(row)) report.reportData.rows.push(row)
            })
        }, { deep: true })

        return {
            configureReport,    
            logo: props.params.logo,
            reportData: report.reportData,
            rows: report.rows,
            columns: report.columns,
            title: report.title,
            period: report.period,
            dateGenerated: report.dateGenerated,
            refresh: report.updateReportData,
            toPDF: () => {
                const { columns, rows } = toExportableFormat(
                report.reportData.other.activeColumns, 
                report.reportData.other.activeRows
              )
              toTablePDF(columns, rows, report.reportDescription())
            },
            toCSV: () => {
                const { columns, rows } = toExportableFormat(
                report.reportData.other.activeColumns, 
                report.reportData.other.activeRows
              )
              toCsv(columns, report.appendReportStamp(rows), report.reportDescription())
            }
        }
    }
})
